import utils, { randomColor, randomIntFromRange } from './utils'

const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight

const mouse = {
  x: innerWidth / 2,
  y: innerHeight / 2
}

const colors = ['#2185C5', '#7ECEFD', '#FFF6E5', '#FF7F66']

const GRAVITY = 1;
const FRICTION = 0.59;

// Event Listeners
addEventListener('mousemove', (event) => {
  mouse.x = event.clientX
  mouse.y = event.clientY
})

addEventListener('resize', () => {
  canvas.width = innerWidth
  canvas.height = innerHeight

  init()
})
addEventListener('click', () => {
  init()
})

// Objects
class Ball {
  constructor(x, y,dx, dy, radius, color) {
    this.x = x;
    this.y = y;
    this.dx = dx;
    this.dy = dy;
    this.radius = radius;
    this.color = color;
  }

  draw() {
    c.beginPath()
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    c.fillStyle = this.color
    c.fill()
    c.stroke()
    c.closePath()
  }

  update() {
    if (this.y + this.radius > canvas.height) {
        this.dy = -this.dy * GRAVITY * FRICTION; // friccion  g = 0.9 m/S
    } else {
      this.dy +=1;
    }
    if (this.x + this.radius > canvas.width || this.x- this.radius < 0) {
      this.dx = -this.dx * GRAVITY * FRICTION;
    }
    this.x += this.dx; // Bajar una posicion
    this.y += this.dy; // Bajar una posicion
    this.draw();
  }
}

// Implementation
let ball;
let ballArray;


function init() {
  ballArray = [];
  for(let i=0;i< 100;i++ ) {
    let radius = randomIntFromRange(1,40);
    let x =randomIntFromRange(radius,canvas.width -radius);
    let y =randomIntFromRange(0,canvas.height - radius);
    let dx = randomIntFromRange(4,-4);
    let dy = randomIntFromRange(4,-4);
    let color =randomColor(colors);
    ball = new Ball (x,y,dx, dy, radius, color );
    ballArray.push(ball);
  }
}

// Animation Loop
function animate() {
  requestAnimationFrame(animate)

  c.clearRect(0, 0, canvas.width, canvas.height)

  for (let i=0;i<ballArray.length;i++) {
    ballArray[i].update();
  }

  // ball.update();
}

init()
animate()
